%main {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0;

  @include onTablet {
    font-size: 16px;
  }
}

%h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0;
  color: #333;

  @include onTablet {
    font-size: 36px;
    line-height: 150%;
  }

  @include onDesktop {
    font-size: 60px;
    line-height: 130%;
  }
}

%h2 {
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #344f10;

  @include onDesktop {
    font-size: 36px;
    line-height: 40px;
  }
}

%h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #344f10;

  @include onDesktop {
    font-size: 24px;
  }
}

%h4 {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

%decorative {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0;
  margin: 0;
  padding: 0;
}

%button {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;
}

%menu {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0;
  text-transform: capitalize;
}

%text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0;
}

%text2 {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

%text3 {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

%input {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0;
  text-transform: capitalize;

  @include onDesktop {
    font-size: 18px;
  }
}

%contacts {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0;

  @include onDesktop {
    font-size: 16px;
  }
}

%copyright {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0;
  text-transform: capitalize;

  @include onTablet {
    line-height: 150%;
  }

  @include onDesktop {
    font-size: 18px;
  }
}

%tab {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  @include onDesktop {
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
  }

  %checkout {
    margin: 0 0 20px;
    padding-bottom: 5px;
    font-weight: 600;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-bottom: 1px solid #bdbdbd;
    color: #8e8d8c;

    @include onTablet {
      margin: 0 0 60px;
      width: 218px;
    }
  }

  %title {
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #333;
  }
}
